$big: 328px;
.donat {
  .theme_light {

  }

  &-svg {
    @media only screen and (orientation: landscape) and (max-height: 415px), only screen and (max-width: 321px) {
      width: 240px !important;
      height: 240px !important;
    }
    @media only screen and (min-width: 767px) {
      width: $big;
      height: $big;
    }

    svg {
      @media only screen and (orientation: landscape), only screen and (max-width: 321px) {
        width: 240px;
        height: 240px;
      }
      @media only screen and (min-width: 767px) and (min-height: 415px) {
        width: $big;
        height: $big;
      }
    }

  }

  &-container {
    position: relative;
  }

  &-text {
    width: 180px;
    @media only screen and (orientation: landscape) and (max-width: 766px), only screen and (max-width: 321px) {
      width: 120px;
    }

    &_diff {
      @media only screen and (orientation: landscape) and (max-width: 766px), only screen and (max-width: 321px) {
        font-size: 16px;
        line-height: 18px;
        font-weight: 500;
      }
      @media only screen and (orientation: landscape), only screen and (max-width: 321px){
        font-size: 16px;
        line-height: 18px;
      }
      font-weight: 700;
      font-size: 20px;
      line-height: 22px;
      color: #918F8A;
    }

    &_total {
      @media only screen and (orientation: landscape), only screen and (max-width: 321px) {
        font-size: 20px;
        line-height: 22px;
      }
      font-weight: bold;
      font-size: 30px;
      line-height: 32px;
      margin-bottom: 6px;
      @media only screen and (orientation: portrait) {
        margin-bottom: 8px;
      }
    }
  }

  &-text-container {
    width: 328px;
    height: 328px;

    @media only screen and (orientation: landscape), only screen and (max-width: 321px) {
      width: 240px;
      height: 240px;
    }
    @media only screen and (min-width: 767px) and (min-height: 415px) {
      width: 328px;
      height: 328px;
    }
    position: absolute;
    top: 0;
    left: 0;
    z-index: 12;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

