@import "./colors";

@font-face {
  font-family: 'Roboto';
  src: url("./assets/fonts/Roboto-Bold.ttf") format("woff");
  font-weight: bold;

}

@font-face {
  font-family: 'Roboto';
  src: url("./assets/fonts/Roboto-Medium.ttf") format("woff");
  font-weight: 500;
}

@font-face {
  font-family: 'Roboto';
  src: url("./assets/fonts/Roboto-Regular.ttf") format("woff");;
  font-weight: 400;
}

body {
  font-family: 'Roboto', serif;
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: 0;
  text-align: center;
  color: $white;
  background: radial-gradient(70.24% 70.24% at 49.87% 29.76%, rgba(19, 14, 0, 0.9) 0%, #000000 100%);
  //background: white;
}

.theme_light {
  color: $black;
  background: $background_white;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html, body {
  height: 100%;

  display: flex;
  flex-direction: column;
}

body > * {
  flex-shrink: 0;
}

[data-action="go-prev"] {
background: transparent;
  height: 100vh;
  width: 50%;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 9999;
}

[data-action="go-next"] {
  background: transparent;
  height: 100vh;
  width: 50%;
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 999;
}